import { Button, Stack, Typography } from '@mui/material'
import { DataGrid } from '@mui/x-data-grid'
import { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getWebs } from 'redux/slices/CMS/Web'
import { getForms } from 'redux/slices/Form'

export default function FormPage() {
	const dispatch = useAppDispatch()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)
	const status = useAppSelector((s) => s.Form.status)
	const list = useAppSelector((s) => s.Form.list)
	useEffect(() => {
		if (status === 'init') dispatch(getForms())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const webs = useAppSelector((s) => s.CMS.Web.list)
	const webStatus = useAppSelector((s) => s.CMS.Web.status)
	useEffect(() => {
		if (webStatus === 'init') dispatch(getWebs())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	if (!tokenInfo) return null

	return (
		<Stack gap={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					sx={{
						fontWeight: 600,
					}}
					variant="h5"
				>
					全てのフォーム一覧
				</Typography>
				<Link to="/form/create">
					<Button
						variant="outlined"
						sx={{
							minWidth: 120,
						}}
					>
						新規作成
					</Button>
				</Link>
			</Stack>
			<DataGrid
				rows={list}
				columns={[
					{
						field: 'name',
						headerName: 'フォーム名',
						flex: 1,
						renderCell: (v) => {
							return (
								<Link to={`/form/${v.row.id}`}>
									<Typography variant="caption" mr={1}>
										{v.row.name}
									</Typography>
								</Link>
							)
						},
					},
					{
						field: 'webId',
						headerName: '使用ウェブ',
						flex: 1,
						renderCell: (v) => {
							const web = webs.find((f) => f.id === v.row.webId)
							return (
								<Link to={`/cms/${v.row.webId}/page`}>
									<Typography variant="caption" mr={1}>
										{web?.name || '--'}
									</Typography>
								</Link>
							)
						},
					},
					// {
					// 	field: 'status',
					// 	headerName: 'ステータス',
					// 	flex: 1,
					// 	renderCell: (v) => {
					// 		return (
					// 			<Typography variant="caption" mr={1}>
					// 				{v.row.status}
					// 			</Typography>
					// 		)
					// 	},
					// },
				]}
				initialState={{
					pagination: {
						paginationModel: { page: 0, pageSize: 10 },
					},
				}}
				pageSizeOptions={[10, 20, 30, 50]}
				sx={{
					'& .MuiDataGrid-cell:focus': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:focus-within': {
						outline: 'none',
					},
					'& .MuiDataGrid-cell:hover': {},
					'& .MuiDataGrid-columnHeaders': {
						bgcolor: 'primary.dark',
						color: '#fff',
					},
					'& .MuiDataGrid-columnHeaders svg': {
						color: '#fff',
					},
				}}
				density="compact"
				hideFooterSelectedRowCount
				disableColumnMenu
				disableRowSelectionOnClick
			/>
		</Stack>
	)
}
